<template>
    <div class="demo">
        <comHeader name="switch"/>
        <h1>Switch Demo</h1>
        <h2></h2>
        <pre>import Switch from '../components/switch/index'</pre>
        <pre>&lt;Switch v-model="checked">&lt;/Switch></pre>
        <h3>1</h3>
        <vSwitch v-model="checked"></vSwitch>
        {{checked}}
        <h3>2</h3>
        <pre>
         &lt;Switch>
         &lt;span slot="open">ON&lt;/span>
         &lt;span slot="close">OFF&lt;/span>
         &lt;Switch></pre>
        <vSwitch>
            <span slot="open">ON</span>
            <span slot="close">OFF</span>
        </vSwitch>
        <vSwitch>
            <span slot="open"></span>
            <span slot="close"></span>
        </vSwitch>
        <h3>3</h3>
        <p>v-model</p>
        <vSwitch :change="change" v-model="checked2"></vSwitch>
        {{checked2}}
        <h3>4</h3>
        <vSwitch :disabled="disabled"></vSwitch>
        <h2>API</h2>
        <h3>Switch props</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>class</td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <td>value</td>
                <td>Booleanfalse</td>
                <td> v-model </td>
            </tr>
            <tr>
                <td>disabled</td>
                <td>Booleanfalse</td>
                <td></td>
            </tr>
            <tr>
                <td>change</td>
                <td>Function</td>
                <td></td>
            </tr>
        </table>
        <h3>Switch slot</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>open</td>
                <td></td>
            </tr>
            <tr>
                <td>close</td>
                <td></td>
            </tr>
        </table>
    </div>
</template>
<script>
    import vSwitch from '../components/switch/index'
    export default {
        name: 'switch',
        path: '/switch',
        data () {
            return {
                checked: false,
                checked2: true,
                disabled: true
            }
        },
        components: {vSwitch},
        mounted(){
        },
        methods: {
            change(v){
                this.checked2 = v;
            }
        },
        computed: {},
        filters: {}
    }
</script>


