<template>
    <div class="demo">
        <comHeader name="menu"/>
        <h1>Menu Demo</h1>
        <h2></h2>
        <pre>import MenuNav from '../components/menu/index'</pre>
        <pre>&lt;MenuNav :data="data" value="a3">&lt;/MenuNav></pre>
        <h3></h3>
        <p>1</p>
        <p>2value=nameactivevaluenameactive</p>
        <p>3childhas-child</p>
        <p>4childtype=hhovertype=vhover</p>
        <p>5type=vvalue=name</p>
        <p>6css3</p>
        <h3>1</h3>
        <MenuNav :data="data" value="a3"></MenuNav>
        <h3>2</h3>
        <MenuNav :data="data2" value="a6"></MenuNav>
        <h3>3</h3>
        <MenuNav :data="data3" value="a6" type="v" :accordion="true"></MenuNav>
        <h3>4</h3>
        <MenuNav :data="data3" value="a6" type="v"></MenuNav>
        <h3>5</h3>
        <p></p>
        <MenuNav :data="data3" :value="value5" type="v" :onSelect="onSelect"></MenuNav>
        <h2>API</h2>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>class</td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <td>type</td>
                <td>Stringh</td>
                <td> hhorizontal  vvertical</td>
            </tr>
            <tr>
                <td>data</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>value</td>
                <td>String</td>
                <td> name </td>
            </tr>
            <tr>
                <td>accordion</td>
                <td>Booleanfalse</td>
                <td>mode=v</td>
            </tr>
            <tr>
                <td>onSelect</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>router</td>
                <td>Booleantrue</td>
                <td></td>
            </tr>
        </table>
        <h3>data</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>title</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>href</td>
                <td>String</td>
                <td>url</td>
            </tr>
            <tr>
                <td>target</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>name</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>child</td>
                <td>Array</td>
                <td></td>
            </tr>
        </table>
    </div>
</template>
<script>
    import MenuNav from '../components/menu/index'
    export default {
        name: 'menu',
        path: '/menu',
        data(){
            return {
                data: [
                    {title: '', href: '/', name: 'a1'},
                    {title: '', href: 'test/product.html', name: 'a2'},
                    {title: '', href: 'javascript:;', name: 'a3'},
                    {title: '', href: 'javascript:;', name: 'a4'}
                ],
                data2: [
                    {title: '', href: '/', name: 'a1'},
                    {
                        title: '',
                        href: 'test/product.html',
                        name: 'a2',
                        child: [{title: '', href: 'javascript:;'}, {title: '2', href: 'javascript:;'}]
                    },
                    {title: '', href: 'javascript:;', name: 'a3'},
                    {
                        title: '', href: 'javascript:;', name: 'a4', child: [
                        {title: '1', href: ''},
                        {title: '2', href: '', name: 'a6'},
                        {title: '2', href: '', name: 'a7'}
                    ]
                    }
                ],
                data3: [
                    {title: '', href: '/', name: 'a1'},
                    {
                        title: '', name: 'a2', href: 'javascript:;', child: [
                        {title: '', href: 'javascript:;', name: 'a21'},
                        {title: '2', name: 'a23', href: 'javascript:;'}
                    ]
                    },
                    {title: '', href: 'javascript:;', name: 'a3'},
                    {
                        title: '', href: 'javascript:;', name: 'a4', child: [
                        {title: '1', href: 'javascript:;', name: 'a41'},
                        {title: '2', href: 'javascript:;', name: 'a6'},
                        {title: '2', href: 'javascript:;', name: 'a7'}
                    ]
                    }
                ],
                value5: 'a6'
            }
        },
        props: [],
        mounted(){
        },
        components: {MenuNav},
        methods: {
            onSelect(item){
                this.value5 = item.name;
            }
        },
        computed: {}
    }
</script>


