<template>
    <div class="demo">
        <comHeader name="form"/>
        <h1>Form Demo</h1>
        <h2>Use</h2>
        <pre>import gForm from '../components/form/form'</pre>
        <pre>&lt;gForm :data="data">&lt;/gForm></pre>
        <pre>
        {
            type: 'input',
            name: 'userName',
            value: this.userName,
            item: {label: "userName"},
            control: {placeholder: 'please enter your name'}
        }
        </pre>
        <h3>1</h3>
        <gForm :data="_formData()" @input="_input" ref="form" @getValue="_getValue">
            <div slot="slotName" class="con">slot</div>
            <div slot="slotForm">
                <p>slotformItem</p>
                <p>slotformIteminput</p>
                <pre>import FormItem from '../components/form/formItem'
    import gInput from '../components/input/index'
     &lt;FormItem label="" labelClass="icon-user">
        &lt;gInput v-model="v1" placeholder="slot">&lt;/gInput>
     &lt;/FormItem></pre>
                <FormItem label="" labelClass="icon-user">
                    <gInput v-model="v1" placeholder="slot"></gInput>
                </FormItem>
            </div>
        </gForm>
        <button @click="_buttonClick" class="btn-confirm btn">button</button>
        <pre>
         
         &lt;gForm :data="_formData()" @input="_input" ref="form" @getValue="_getValue">&lt;/gForm>
         
         &lt;button @click="_buttonClick" class="btn-confirm btn" >button&lt;/button>
         
         _buttonClick(){
                //
                this.$refs.form.submit();
            },
         _getValue(valueArray, tipArray){
                console.log(valueArray);
                console.log(tipArray);
            }</pre>
        <h2>API</h2>
        <h3>Form</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>class</td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <td>data</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>input</td>
                <td>Function</td>
                <td>namevalue</td>
            </tr>
            <tr>
                <td>inputBlur</td>
                <td>Boolean|true</td>
                <td>inputtype='input'</td>
            </tr>
            <tr>
                <td>verification</td>
                <td>Boolean|true</td>
                <td></td>
            </tr>
            <tr>
                <td>submit</td>
                <td></td>
                <td>this.$refs.form.submit()getValue</td>
            </tr>
            <tr>
                <td>getValue</td>
                <td>Function</td>
                <td>
                    namevaluegetValuerefthis.$refs.form.submit()
                </td>
            </tr>
        </table>
        <h3>data</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>type</td>
                <td>String</td>
                <td>
                    inputradiocheckboxselecttextareadatePickercascadertextslot(slotnamenameslot='name')btnCodeimgCodeswitch
                </td>
            </tr>
            <tr>
                <td>name</td>
                <td>String</td>
                <td>itemnamevaluevalue</td>
            </tr>
            <tr>
                <td>value</td>
                <td></td>
                <td>type="text"valuetype="slot"value</td>
            </tr>
            <tr>
                <td>item</td>
                <td>Array</td>
                <td>formItemformItem</td>
            </tr>
            <tr>
                <td>control</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>validate</td>
                <td>Array</td>
                <td>validate</td>
            </tr>
            <tr>
                <td>other</td>
                <td>null</td>
                <td></td>
            </tr>
            <tr>
                <td>code</td>
                <td>Array</td>
                <td>classclicktype='btnCode'text,text2:
                    x,text3:,time:type='imgCode'src
                </td>
            </tr>
        </table>
        <h3>validate</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>type</td>
                <td>requireddigitsnumbermobilemailtelfaxrulefnmaxLengthminLength</td>
            </tr>
            <tr>
                <td>msg</td>
                <td></td>
            </tr>
            <tr>
                <td>maxLength</td>
                <td>type=maxLength</td>
            </tr>
            <tr>
                <td>minLength</td>
                <td>type=minLength</td>
            </tr>
        </table>
        <h3>formItem</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>class</td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <td>label</td>
                <td>String</td>
                <td>label</td>
            </tr>
            <tr>
                <td>labelClass</td>
                <td>String</td>
                <td>label</td>
            </tr>
            <tr>
                <td>showTip</td>
                <td>Booleanfalse</td>
                <td></td>
            </tr>
            <tr>
                <td>tipType</td>
                <td>Stringfailure</td>
                <td>successfailure</td>
            </tr>
            <tr>
                <td>tipText</td>
                <td>String</td>
                <td></td>
            </tr>
        </table>
    </div>
</template>
<script>
    import gForm from '../components/form/index'
    import FormItem from '../components/form/formItem'
    import gInput from '../components/input/index'
    export default {
        name: 'form',
        path: "/form",
        data () {
            return {
                v1: "",
                /*validate: [
                 {type: "required", msg: ""},
                 {type: "maxLength", msg: "10", maxLength: 10},
                 {type: "minLength", msg: "3", minLength: 3}
                 ],*/
                userName: 'userName0',
                password: ''
            }
        },
        props: {},
        components: {gForm, FormItem, gInput},
        methods: {
            _formData(){
                let th = this;
                return [
                    [
                        {
                            type: 'input',
                            name: 'userName',//namevaluedatadataname
                            value: this.userName,
                            item: {label: "userName"},
                            control: {placeholder: 'please enter your name'}
                        },
                        {
                            type: 'input',
                            name: 'userName2',
                            value: '',
                            item: {label: "userName", class: 'auto-height'},
                            control: {placeholder: '310'},
                            validate: [
                                {type: "required", msg: ""},
                                {type: "maxLength", msg: "10", maxLength: 10},
                                {type: "minLength", msg: "3", minLength: 3}
                            ]
                        },
                        {
                            type: 'input',
                            name: 'password',
                            value: this.password,
                            item: {label: 'password'},
                            control: {type: 'password', placeholder: '', showEye: true, clearIcon: true},
                            validate: [
                                {type: "required", msg: ""}
                            ]
                        },
                        {
                            type: 'input',
                            name: 'password2',
                            value: '',
                            item: {label: 'password2'},
                            control: {type: 'password', placeholder: ''},
                            validate: [
                                {type: "required", msg: ""},
                                {
                                    type: 'fn', msg: '', validator(v) {
                                    if (v == th.password) {
                                        return true
                                    } else {
                                        return false
                                    }
                                }
                                }
                            ]
                        },
                        {
                            type: 'input',
                            name: 'digits',
                            value: '',
                            item: {label: 'digits'},
                            control: {placeholder: ''},
                            validate: [
                                {type: "digits", msg: ""}
                            ]
                        },
                        {
                            type: 'input',
                            name: 'number',
                            value: '',
                            item: {label: 'number'},
                            control: {placeholder: ''},
                            validate: [
                                {type: "number", msg: ""}
                            ]
                        },
                        {
                            type: 'input',
                            name: 'mobile',
                            value: '',
                            item: {label: 'mobile'},
                            control: {placeholder: ''},
                            validate: [
                                {type: "mobile", msg: ""}
                            ]
                        },
                        {
                            type: 'input',
                            name: 'mail',
                            value: '',
                            item: {label: 'mail'},
                            control: {placeholder: ''},
                            validate: [
                                {type: "mail", msg: ""}
                            ]
                        },
                        {
                            type: 'input',
                            name: 'tel',
                            value: '',
                            item: {label: 'tel'},
                            control: {placeholder: ''},
                            validate: [
                                {type: "tel", msg: ""}
                            ]
                        },
                        {
                            type: 'input',
                            name: 'fax',
                            value: '',
                            item: {label: 'fax'},
                            control: {placeholder: ''},
                            validate: [
                                {type: "fax", msg: ""}
                            ]
                        },
                        {
                            type: 'input',
                            name: 'rules',
                            value: '',
                            item: {label: 'rules'},
                            control: {placeholder: '1120'},
                            validate: [
                                {type: "required", msg: ""},
                                {type: 'rule', msg: "1120", rule: "/^(?:[1-9][0-9]?|1[01][0-9]|120)$/"}
                            ]
                        },
                        {
                            type: 'input',
                            name: 'fn',
                            value: '',
                            item: {label: 'fn'},
                            control: {placeholder: '123'},
                            validate: [
                                {
                                    type: "fn", msg: "", validator: function (v) {
                                    if (v == '123') {
                                        return true
                                    } else {
                                        return false
                                    }
                                }
                                }
                            ]
                        },
                        {
                            type: 'input',
                            name: 'other',
                            value: '',
                            item: {label: 'other'},
                            control: {placeholder: 'otherinput'},
                            other: 'kg'
                        },
                        {
                            type: 'btnCode',
                            name: 'btnCode',
                            value: '',
                            item: {label: "inputCode"},
                            control: {placeholder: ''},
                            code: {
                                text: '',
                                text2: 'x',
                                text3: '',
                                time: '30',
                                class: 'btn-class',
                                click: this._inputCodeClick
                            }
                        },
                        {
                            type: 'imgCode',
                            name: 'imgCode',
                            value: '',
                            item: {label: "imgCode"},
                            control: {placeholder: ''},
                            code: {src: 'static/images/code.jpg', class: 'btn-class', click: this._imgCodeClick}
                        }
                    ],
                    [
                        {
                            type: 'radio',
                            name: 'sex',
                            value: '1',
                            item: {label: 'sex'},
                            control: {
                                data: [
                                    {text: '', value: '1'},
                                    {text: '', value: '0'}
                                ]
                            },
                            other: ''
                        },
                        {
                            type: 'radio',
                            name: 'sex2',
                            value: '',
                            item: {label: 'sex2'},
                            control: {
                                data: [
                                    {text: '', value: '1'},
                                    {text: '', value: '0'}
                                ]
                            },
                            other: 'form-group'
                        }
                    ],
                    [
                        {
                            type: 'checkbox',
                            name: 'checkbox',
                            value: ['a1', 'a3', 'a6'],
                            item: {label: 'checkbox'},
                            control: {
                                data: [
                                    {text: '1', value: 'a1'},
                                    {text: '2', value: 'a2'},
                                    {text: '3', value: 'a3'},
                                    {text: '4', value: 'a4'},
                                    {text: '', value: 'a5', disabled: true},
                                    {text: '', value: 'a6', disabled: true}
                                ]
                            }
                        },
                        {
                            type: 'checkbox',
                            name: 'checkbox2',
                            value: [],
                            item: {label: 'checkbox2'},
                            control: {
                                data: [
                                    {text: '1', value: 'a1'},
                                    {text: '2', value: 'a2'},
                                    {text: '3', value: 'a3'},
                                    {text: '4', value: 'a4'}
                                ]
                            },
                            validate: [
                                {type: "required", msg: "checkbox2"}
                            ]
                        }
                    ],
                    [
                        {
                            type: 'select',
                            name: 'select',
                            value: '',
                            item: {label: 'select'},
                            control: {
                                placeholder: '',
                                option: [
                                    {text: '11', value: '1'},
                                    {text: '212', value: '2'},
                                    {text: '3123', value: '30', disabled: true},
                                    {text: '4', value: '4'},
                                    {text: '5', value: '5'},
                                    {text: '6', value: '7'},
                                    {text: '8', value: '8'},
                                    {text: '9', value: '9'}
                                ]
                            },
                            validate: [
                                {type: "required", msg: "select"}
                            ]
                        },
                        {
                            type: 'select',
                            name: 'select2',
                            value: '8',
                            item: {label: 'select2'},
                            control: {
                                placeholder: '',
                                option: [
                                    {text: '11', value: '1'},
                                    {text: '212', value: '2'},
                                    {text: '3123', value: '30', disabled: true},
                                    {text: '4', value: '4'},
                                    {text: '5', value: '5'},
                                    {text: '6', value: '7'},
                                    {text: '8', value: '8'},
                                    {text: '9', value: '9'}
                                ]
                            },
                            validate: [
                                {type: "required", msg: "select"}
                            ]
                        }
                    ],
                    [
                        {
                            type: 'textarea',
                            value: '',
                            name: 'textarea',
                            item: {label: 'textarea', class: 'auto-height'},
                            control: {placeholder: "placeholder", width: '330px'}
                        }
                    ],
                    {
                        type: 'text',
                        value: 'name',
                        item: {label: 'text'},
                        control: {class: 'text'}
                    },
                    {
                        type: 'slot',
                        name: 'slotName'
                    },
                    {
                        type: 'slot',
                        name: 'slotForm'
                    },
                    {
                        type: 'datePicker',
                        value: '',
                        name: 'datePicker',
                        item: {label: 'datePicker'},
                        control: {placeholder: ''}
                    },
                    {
                        type: 'switch',
                        value: false,
                        name: 'switch',
                        item: {label: 'switch'},
                        control: {}
                    }
                ]
            },
            _input(k, v){
                // this[k] = v;
            },
            _inputCodeClick(){
                console.log('_inputCodeClick');
            },
            _imgCodeClick(){
                console.log('_imgCodeClick');
            },
            _buttonClick(){
                //
                this.$refs.form.submit();
            },
            _getValue(valueArray, tipArray){
                console.log(valueArray);
                console.log(tipArray);
            }
        },
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


