<template>
    <div class="demo">
        <comHeader name="loading"/>
        <h1>Dialog Demo</h1>
        <h2></h2>
        <pre>
           import Loading from '../components/loading/index'</pre>
        <pre>
           &lt;Loading>&lt;/Loading></pre>
        <h3>1</h3>
        <p>loadingloadingabsoluteloadingrelative</p>
        <div class="demo-1">
            <Loading></Loading>
        </div>
        <h3>2</h3>
        <p></p>
        <div class="demo-1">
            <Loading :maskLayer="true"><p class="tips"></p></Loading>
        </div>
        <h3>3</h3>
        <p>nodeNum</p>
        <div class="demo-1">
            <Loading class="style-2" :nodeNum="3"></Loading>
        </div>
        <h3>4</h3>
        <p>this.$loading()loading</p>
        <div class="demo-1">
            <p @click="openLoading"><a href="javascript:;">loading</a></p>
            <p>5this.$loading.close()</p>
            <pre>openLoading(){<br>
                this.$loading({<br>
                    className: 'abc',<br>
                    maskLayer:true<br>
                });<br>
               setTimeout(()=>{<br>
                   this.$loading.close()<br>
               },5000)
            }</pre>
        </div>
        <h2>API</h2>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>show</td>
                <td>Boolean | true</td>
                <td></td>
            </tr>
            <tr>
                <td>maskLayer</td>
                <td>Boolean | false</td>
                <td></td>
            </tr>
            <tr>
                <td>nodeNum</td>
                <td>Number | 0</td>
                <td></td>
            </tr>
            <tr>
                <td>className</td>
                <td>String</td>
                <td>class</td>
            </tr>
        </table>
    </div>
</template>
<script type="text/ecmascript-6">
    //
    //import Loading from '../components/loading/index'
    export default {
        name: 'loading',
        path: "/loading",
        data () {
            return {
            }
        },
        components: {},
        mounted(){
            //this.$loading({className: 'abc'})
        },
        methods: {
            openLoading(){
                this.$loading({
                    className: 'abc',
                    maskLayer:true
                });
               setTimeout(()=>{
                   this.$loading.close()
               },5000)
            }
        },
        computed: {},
        filters: {}
    }
</script>
<style>
    .demo-1{ width: 100%; min-height: 120px; border: 1px solid #ddd; position: relative; }
    .demo-1 .tips{ text-align: center; padding-top: 80px; }
    /**/
    .style-2{ text-align: center }
    .style-2:before{ display: none }
    .style-2 .loading-content > span{ width: 30px; height: 30px; background-color: #67CF22; border-radius: 100%; display: inline-block; animation: bouncedelay 1.4s infinite ease-in-out both; }
    .style-2 span:nth-child(1){ animation-delay: -0.32s; }
    .style-2 span:nth-child(2){ animation-delay: -0.16s; }
    @keyframes bouncedelay{
        0%, 80%, 100%{
            transform: scale(0.0);
        }
        40%{
            transform: scale(1.0);
        }
    }
</style>


