<template>
    <div class="demo">
        <h1>Upload Demo</h1>
        <comHeader name="backTop"/>
        <h2>Use</h2>
        <pre>import Upload from '../components/upload/index'</pre>
        <pre>&lt;Upload action="">&lt;/Upload></pre>
        <h2></h2>
        <p>a.FormData</p>
        <p>b.</p>
        <p>c.PHP</p>
        <pre>
            //php
            header("Access-Control-Allow-Origin: *");
            $filename = $_FILES['file']['name'];
            move_uploaded_file($_FILES['file']['tmp_name'],'upload/'.$filename);
            echo json_encode('upload/'.$filename);</pre>
        <p></p>
        <pre>
            $filename = $_FILES['file']['name'];
            foreach ($filename as $k=>$names){
            	move_uploaded_file($_FILES['file']['tmp_name'][$k],'upload/'.$filename[$k]);
            }
            echo ''</pre>
        <h3>1</h3>
        <Upload :action="action" @success="_success">
            
        </Upload>
        <h3>2Input</h3>
        <div>
            <input type="text" v-model="inputUpload" class="input-control">
            <Upload :action="action" @success="_success2">
                
            </Upload>
        </div>
        <h3>3</h3>
        <div class="img-upload">
            <img :src="imgUpload" width="120" height="120">
            <span :style="{width:imgProgress}"></span>
        </div>
        <Upload :action="action" @success="_success3" :showProgress="false" @progress="_progress3">
            
        </Upload>
        <h3>4</h3>
        <p>jpg200kb</p>
        <p>:format="['jpg']" :maxSize="200"</p>
        <Upload :action="action" @success="_success" @checkFail="_checkFail4" :format="['jpg']" :maxSize="200">
            
        </Upload>
        <h3>5</h3>
        <p></p>
        <Upload :multiple="true" :action="action" @success="_success" :format="['jpg']" :maxSize="200" @checkFail="_checkFail5" @delImg="_delImg5">
            
        </Upload>
        <h3>6</h3>
        <Upload :action="action" @success="_success" :showList="false" :multiple="true" >
            
        </Upload>
        <h2>API</h2>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>action</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>multiple</td>
                <td>Booleanfalse</td>
                <td></td>
            </tr>
            <tr>
                <td>showProgress</td>
                <td>Booleantrue</td>
                <td></td>
            </tr>
            <tr>
                <td>name</td>
                <td>Stringfile</td>
                <td></td>
            </tr>
            <tr>
                <td>accept</td>
                <td>String</td>
                <td>h5</td>
            </tr>
            <tr>
                <td>format</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>maxSize</td>
                <td>Number</td>
                <td>kb</td>
            </tr>
            <tr>
                <td>checkFail</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>progress</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>success</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>error</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>delImg</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>showList</td>
                <td>Booleantrue</td>
                <td>multiple=true</td>
            </tr>
        </table>
    </div>
</template>
<script type="text/ecmascript-6">
    import Upload from '../components/upload/index'
    export default {
        name: 'upload',
        path: '/upload',
        data () {
            return {
                images: [],
                progress: '',
                action: 'http://172.17.2.18/vue/upload.php',
                inputUpload: '',
                imgUpload: '',
                imgProgress: ''
            }
        },
        props: {},
        components: {Upload},
        methods: {
            _success(data){
                console.log('');
                console.log(data);
            },
            _error(data){
                console.log('');
                console.log(data);
            },
            _progress(num){
                console.log('' + num);
            },
            _success2(data){
                this.inputUpload = data;
            },
            _success3(data){
                this.imgUpload = 'http://172.17.2.18/vue/' + data;
            },
            _progress3(txt){
                this.imgProgress = txt;
            },
            _checkFail4(txt){
                alert(txt);
            },
            _checkFail5(txt){
                //
                console.log(txt);
            },
            _delImg5(src){
                //
                console.log('');
                console.log(src);
            }
        },
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>
<style>
    /**/
    .img-upload{ position: relative; width: 120px; height: 120px; margin-bottom: 10px }
    .img-upload span{ position: absolute; left: 0; bottom: 0; height: 5px; background: #1dc116; }
</style>


