<template>
    <div class="demo">
        <comHeader name="table"/>
        <h1>Table Demo</h1>
        <h2></h2>
        <pre>
           import dateTable from '../components/table/index'</pre>
        <pre>
           &lt;dateTable :columns="columns1" :data="data1">&lt;/dateTable></pre>
        <h3>1</h3>
        <dateTable :columns="columns1" :data="data1"></dateTable>
        <h3>2</h3>
        <p>stripe:false</p>
        <p>hover:false</p>
        <p>border:false</p>
        <dateTable :columns="columns1" :data="data1" :stripe="stripe" :border="border" :hover="hover"></dateTable>
        <h3>3</h3>
        <pre>
            //address
            width:'100px',//
            className:'col',//
            ellipsis:true//
            //age
            render: (h, v)=> {
                  return h('div', v + '')//divdivreturn h('div',{attrs:{className:'aa'}}, v + '')
                        }</pre>
        <p>render</p>
        <pre>
             return h('Icon', {
                    props: {//props
                       type: 'person'
                            }
                     });</pre>
        <dateTable :columns="columns2" :data="data1"></dateTable>
        <h3>4</h3>
        <p>sortable="true"</p>
        <dateTable :columns="columns3" :data="data1"></dateTable>
        <h3>5</h3>
        <p>height="150"</p>
        <p>css3translateYthdivtable-cell</p>
        <dateTable :columns="columns1" :data="data1" height="150px"></dateTable>
        <h3>6</h3>
        <p>css</p>
        <p>width:div</p>
        <p>innerWidth:tablewidth</p>
        <dateTable :columns="columns1" :data="data1" height="150px" width="100%" innerWidth="140%"></dateTable>
        <h2>API</h2>
        <h3>Table</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>columns</td>
                <td>Array</td>
                <td>columns API</td>
            </tr>
            <tr>
                <td>data</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>class</td>
                <td>String</td>
                <td>tablediv</td>
            </tr>
            <tr>
                <td>width</td>
                <td>String</td>
                <td>tabledivpx/%</td>
            </tr>
            <tr>
                <td>innerWidth</td>
                <td>String</td>
                <td>tablepx/%</td>
            </tr>
            <tr>
                <td>height</td>
                <td>String</td>
                <td>table</td>
            </tr>
            <tr>
                <td>showHeader</td>
                <td>Boolean | true</td>
                <td></td>
            </tr>
            <tr>
                <td>stripe</td>
                <td>Boolean | true</td>
                <td>table-stripe</td>
            </tr>
            <tr>
                <td>border</td>
                <td>Boolean | true</td>
                <td>table-border</td>
            </tr>
            <tr>
                <td>hover</td>
                <td>Boolean | true</td>
                <td>table-hover</td>
            </tr>
        </table>
        <h3>columns</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>title</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>key</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>width</td>
                <td>String</td>
                <td>px/%</td>
            </tr>
            <tr>
                <td>className</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>align</td>
                <td>String</td>
                <td> left right  center </td>
            </tr>
            <tr>
                <td>ellipsis</td>
                <td>Boolean | false</td>
                <td></td>
            </tr>
            <tr>
                <td>sortable</td>
                <td>Boolean | false</td>
                <td></td>
            </tr>
            <tr>
                <td>render</td>
                <td>Function</td>
                <td> Vue  Render  h</td>
            </tr>
        </table>
    </div>
</template>
<script type="text/ecmascript-6">
    import dateTable from '../components/table/index'
    export default {
        name: 'table',
        path: '/table',
        data () {
            return {
                columns1: [
                    {
                        title: 'Name',
                        key: 'name'
                    },
                    {
                        title: 'Age',
                        key: 'age'
                    },
                    {
                        title: 'Address',
                        key: 'address'
                    }
                ],
                data1: [
                    {
                        name: ' A John Brown',
                        age: 18,
                        address: 'New York No. 1 Lake Park'
                    },
                    {
                        name: 'B Jim Green',
                        age: 24,
                        address: 'London No. 1 Lake Park'
                    },
                    {
                        name: 'A Joe Black',
                        age: 30,
                        address: 'Sydney No. 1 Lake Park'
                    },
                    {
                        name: 'C Jon Snow',
                        age: 26,
                        address: 'Ottawa No. 2 Lake Park'
                    }
                ],
                showHeader: true,//
                stripe: false,//
                border: false,//
                hover: false,//,
                columns2: [
                    {
                        title: 'Name',
                        key: 'name'
                    },
                    {
                        title: 'Age',
                        key: 'age',
                        render: (h, v)=> {
                            return h('div', v + '')
                        }
                    },
                    {
                        title: 'Address',
                        key: 'address',
                        width: '100px',//
                        className: 'col',//
                        ellipsis: true//
                    }
                ],
                columns3: [
                    {
                        title: 'Name',
                        key: 'name',
                        sortable: true
                    },
                    {
                        title: 'Age',
                        key: 'age',
                        render: (h, v)=> {
                            return h('div', v + '')
                        },
                        sortable: true
                    },
                    {
                        title: 'Address',
                        key: 'address'
                    }
                ]
            }
        },
        components: {dateTable},
        mounted(){
        },
        methods: {},
        computed: {},
        filters: {}
    }
</script>
<style>
    .col{ background: #2d8cf0 !important; }
</style>


