<template>
    <div class="demo">
        <comHeader name="page"/>
        <h1>Page Demo</h1>
        <h2></h2>
        <pre>import Page from '../components/page/index'</pre>
        <pre>&lt;Page :total="100">&lt;/Page></pre>
        <h3>1</h3>
        <p>{{active}}</p>
        <Page :total="99" v-model="active"></Page>
        <h3>2</h3>
        <pre>
            &lt;Page :total="9" :showElevator="false">
            &lt;span>9&lt;/span>
            &lt;Page></pre>
        <Page :total="9" :showElevator="showElevator">
            <span>9</span>
        </Page>
        <h3>API</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>class</td>
                <td>Stringnull</td>
                <td>div</td>
            </tr>
            <tr>
                <td>value</td>
                <td>Number1</td>
                <td>v-model</td>
            </tr>
            <tr>
                <td>total</td>
                <td>Number0</td>
                <td></td>
            </tr>
            <tr>
                <td>pageSize</td>
                <td>Number10</td>
                <td></td>
            </tr>
            <tr>
                <td>showElevator</td>
                <td>Booleantrue</td>
                <td></td>
            </tr>
            <tr>
                <td>showNum</td>
                <td>Number10</td>
                <td></td>
            </tr>
            <tr>
                <td>change</td>
                <td>Function</td>
                <td>v-model</td>
            </tr>
            <tr>
                <td></td>
                <td></td>
                <td>slot</td>
            </tr>
        </table>
    </div>
</template>
<script>
    import Page from '../components/page/index'
    export default {
        name: 'page',
        path: '/page',
        data () {
            return {
                active:1,
                showElevator:false
            }
        },
        components: {Page},
        mounted(){
        },
        methods: {},
        computed: {},
        filters: {}
    }
</script>


