<template>
    <div class="demo">
        <comHeader name="checkbox"/>
        <h1>Checkbox Demo</h1>
        <h2>Use</h2>
        <pre>import {Checkbox, CheckboxGroup} from '../components/checkbox/index'</pre>
        <pre>&lt;Checkbox v-model="value">text&lt;/Checkbox></pre>
        <h2></h2>
        <p>1</p>
        <p>1.checked2.input:checked</p>
        <h3>1</h3>
        <p>v-modeltruefalse</p>
        <p>true</p>
        <div>
            <Checkbox v-model="v1">true</Checkbox>
            {{v1}}
        </div>
        <div>
            <Checkbox v-model="v2">false</Checkbox>
            {{v2}}
        </div>
        <div>
            <Checkbox text="" v-model="v3"></Checkbox>
            {{v3}}
        </div>
        <div>
            <Checkbox text="" v-model="v4"></Checkbox>
            {{v4}}
        </div>
        <div>
            <Checkbox text="" :disabled='true' v-model="v5"></Checkbox>
            {{v5}}
        </div>
        <h3>2</h3>
        <p>{{group}}</p>
        <pre>&lt;CheckboxGroup :data="groupDate" v-model="group">&lt;/CheckboxGroup></pre>
        <pre>
        group: ['a1', 'a3', 'a6'],
        groupDate: [
           {text: '1', value: 'a1'},
           {text: '2', value: 'a2'},
           {text: '3', value: 'a3'},
           {text: '4', value: 'a4'},
           {text: '', value: 'a5', disabled: true},
           {text: '', value: 'a6', disabled: true}
        ]</pre>
        <div>
            <CheckboxGroup :data="groupDate" v-model="group"></CheckboxGroup>
        </div>
        <h3>3</h3>
        <p></p>
        <p>groupDatevaluegroup</p>
        <p>groupDatevaluegroup</p>
        <p>group</p>
        <h2>API</h2>
        <h3>Checkbox</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>value</td>
                <td></td>
                <td>v-modeltruefalsetrue</td>
            </tr>
            <tr>
                <td>text</td>
                <td>String</td>
                <td>slot</td>
            </tr>
            <tr>
                <td>disabled</td>
                <td>Booleanfalse</td>
                <td></td>
            </tr>
            <tr>
                <td>name</td>
                <td>String</td>
                <td>checkboxname</td>
            </tr>
            <tr>
                <td>change</td>
                <td>Function</td>
                <td></td>
            </tr>
        </table>
        <h3>CheckboxGroup</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>value</td>
                <td>Array</td>
                <td>datavaluev-model</td>
            </tr>
            <tr>
                <td>data</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>name</td>
                <td>String</td>
                <td>checkboxname</td>
            </tr>
            <tr>
                <td>change</td>
                <td>Function</td>
                <td></td>
            </tr>
        </table>
        <h3>CheckboxGroup data</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>text</td>
                <td></td>
            </tr>
            <tr>
                <td>value</td>
                <td>value</td>
            </tr>
            <tr>
                <td>disabled</td>
                <td></td>
            </tr>
        </table>
    </div>
</template>
<script>
    import {Checkbox, CheckboxGroup} from '../components/checkbox/index'
    export default {
        name: 'checkbox',
        path: '/checkbox',
        data () {
            return {
                v1: true,
                v2: false,
                v3: '1',
                v4: '',
                v5: '',
                group: ['a1', 'a3', 'a6'],
                groupDate: [
                    {text: '1', value: 'a1'},
                    {text: '2', value: 'a2'},
                    {text: '3', value: 'a3'},
                    {text: '4', value: 'a4'},
                    {text: '', value: 'a5', disabled: true},
                    {text: '', value: 'a6', disabled: true}
                ]
            }
        },
        props: {},
        components: {Checkbox, CheckboxGroup},
        methods: {},
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


