<template>
    <div class="demo">
        <comHeader name="select"/>
        <h1>Select Demo</h1>
        <h3>divselect</h3>
        <p>placeholderinput</p>
        <h3></h3>
        <p>1ABA</p>
        <p>&nbsp;</p>
        <h3></h3>
        <pre>import SelectDropDown from '../components/select/index'</pre>
        <pre>&lt;SelectDropDown :option="option" v-model="selectValue">&lt;/SelectDropDown></pre>
        <p>1</p>
        <p>{{selectValue}}</p>
        <div>
            <SelectDropDown :option="option" v-model="selectValue" class="abc">
            </SelectDropDown>
        </div>
        <p>2</p>
        <div>
            <SelectDropDown :option="option" placeholder="">
            </SelectDropDown>
        </div>
        <p>3</p>
        <div>
            <SelectDropDown :option="option" :showSearch="true" searchPlaceholder="">
            </SelectDropDown>
        </div>
        <p>4</p>
        <div>
            <SelectDropDown :option="option" :showSearch="true" :disabled="true">
            </SelectDropDown>
        </div>
        <p>5</p>
        <p>{{onChangeText}}</p>
        <div>
            <SelectDropDown :option="option" :onChange="onChange">
            </SelectDropDown>
        </div>
        <p>6</p>
        <div>
            <SelectDropDown :option="option" showNum="5">
            </SelectDropDown>
        </div>
        <p>7</p>
        <div>
            <SelectDropDown :option="option" optionUrl="static/restful/select.json">
            </SelectDropDown>
        </div>
        <p>&nbsp;</p>
        <h3></h3>
        <table class="table-1">
            <tr>
                <th width="80"></th>
                <th width="120"></th>
                <th></th>
            </tr>
            <tr>
                <td>value</td>
                <td></td>
                <td>v-model=""</td>
            </tr>
            <tr>
                <td>placeholder</td>
                <td>String | null</td>
                <td>inputplaceholdervaluevaluevalueplaceholder</td>
            </tr>
            <tr>
                <td>class</td>
                <td>null</td>
                <td></td>
            </tr>
            <tr>
                <td>showNum</td>
                <td>null</td>
                <td></td>
            </tr>
            <tr>
                <td>option</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>optionUrl</td>
                <td>String</td>
                <td>option</td>
            </tr>
            <tr>
                <td>onChange</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>disabled</td>
                <td>false</td>
                <td></td>
            </tr>
            <tr>
                <td>showSearch</td>
                <td>false</td>
                <td></td>
            </tr>
            <tr>
                <td>searchPlaceholder</td>
                <td></td>
                <td>showSearch=true</td>
            </tr>
        </table>
    </div>
</template>
<script>
    import SelectDropDown from '../components/select/index'
    export default {
        name: 'select',
        path: '/select',
        data () {
            return {
                option: [
                    {text: '11', value: '1'},
                    {text: '212', value: '2'},
                    {text: '3123', value: '30', disabled: true},
                    {text: '4', value: '4'},
                    {text: '5', value: '5'},
                    {text: '6', value: '7'},
                    {text: '8', value: '8'},
                    {text: '9', value: '9'}
                ],
                selectValue: '8',
                onChangeText: ''
            }
        },
        components: {SelectDropDown},
        mounted(){

        },
        methods: {
            onChange(item){
                this.onChangeText = `${item.text}${item.value}`;
                // console.log('onChange');
            }
        },
        computed: {},
        filters: {}
    }
</script>


