<template>
    <div class="demo">
        <comHeader name="collapse"/>
        <h1>Collapse Demo</h1>
        <h2></h2>
        <pre>import {Collapse, Panel} from '../components/collapse/'</pre>
        <pre>
          &lt;Collapse value="1">
             &lt;Panel name="1">
                
                &lt;div slot="content">&lt;/div>
             &lt;/Panel>
             &lt;Panel name="2">
                2
             &lt;div slot="content">2&lt;/div>
             &lt;/Panel>
             &lt;Panel name="3">
                3
                &lt;div slot="content">3&lt;/div>
             &lt;/Panel>
           &lt;/Collapse></pre>
        <h3>1</h3>
        <Collapse value="1">
            <Panel name="1">
                
                <div slot="content">
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                </div>
            </Panel>
            <Panel name="2">
                2
                <div slot="content">2</div>
            </Panel>
            <Panel name="3">
                3
                <div slot="content">3</div>
            </Panel>
        </Collapse>
        <h3>2</h3>
        <Collapse value="2" :accordion="true" :change="change">
            <Panel name="1">
                
                <div slot="content"></div>
            </Panel>
            <Panel name="2">
                change
                <div slot="content">2</div>
            </Panel>
            <Panel name="3">
                3
                <div slot="content">3</div>
            </Panel>
        </Collapse>
        <h2>API</h2>
        <h3>Collapse</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>class</td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <td>value</td>
                <td>String</td>
                <td>panelname</td>
            </tr>
            <tr>
                <td>accordion</td>
                <td>Booleanfalse</td>
                <td></td>
            </tr>
            <tr>
                <td>change</td>
                <td>Function</td>
                <td></td>
            </tr>
        </table>
        <h3>Panel</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>class</td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <td>name</td>
                <td>String</td>
                <td>Collapsevalue</td>
            </tr>
        </table>
        <h3>Panel slot</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>content</td>
                <td></td>
            </tr>
        </table>
    </div>
</template>
<script>
    import {Collapse, Panel} from '../components/collapse/'
    export default {
        name: 'collapse',
        path: '/collapse',
        data () {
            return {}
        },
        props: {},
        components: {Collapse, Panel},
        methods: {
            change(name){
                console.log('name=' + name);
            }
        },
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


