<template>
    <div class="demo">
        <h1>Transfer Demo</h1>
        <comHeader name="transfer"/>
        <h2>Use</h2>
        <pre>import Transfer from '../components/transfer/index'</pre>
        <pre>&lt;Transfer :data="data" v-model="value" :titles="['','']">&lt;/Transfer></pre>
        <pre>
            [
                {
                   key: 1,
                   name: '1',
                   disabled: false
                },
                {
                   key: 2,
                   name: '2',
                   disabled: true
                }
            ]</pre>
        <h3>1</h3>
        <p>{{value}}</p>
        <Transfer :data="_transferData()" v-model="value" :titles="['','']"></Transfer>
        <h3>2</h3>
        <Transfer :data="_transferData()" v-model="value2" :titles="['1','2']" :btnText="['','']" @change="_change"></Transfer>
        <h2>API</h2>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>data</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>value</td>
                <td>Array</td>
                <td>v-model</td>
            </tr>
            <tr>
                <td>titles</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>btnText</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>change</td>
                <td>Function</td>
                <td>(0,1)value</td>
            </tr>
        </table>
        <h2>data</h2>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>key</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>name</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>disabled</td>
                <td>Boolean|false</td>
                <td></td>
            </tr>
        </table>
    </div>
</template>
<script>
    import Transfer from '../components/transfer/index'
    export default {
        name: 'transfer',
        path: '/transfer',
        data () {
            return {
                value: [1, 4],
                value2: [4]
            }
        },
        props: {},
        components: {Transfer},
        methods: {
            _transferData(){
                let data = [];
                for (let i = 1; i < 15; i++) {
                    data.push({
                        key: i,
                        name: `${i}`,
                        disabled: i % 4 === 0
                    })
                }
                return data;
            },
            _change(d,v){
                console.log(d);
                console.log(v);
            }
        },
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


