<template>
    <div class="demo">
        <comHeader name="tooltip"/>
        <h1>Tooltip Demo</h1>
        <h2>Use</h2>
        <pre>import Tooltip from '../components/tooltip/index'</pre>
        <pre>&lt;Tooltip content="">&lt;span>&lt;/span>&lt;/Tooltip></pre>
        <h3></h3>
        <p>1<br>
            &lt;div class="tooltip-box">&lt;span>&lt;/span>&lt;/div><br>
            tooltip-boxmargin
        </p>
        <p>2</p>
        <h3>1</h3>
        <div class="tooltip-demo">
            <div class="top">
                <Tooltip content=""><span></span></Tooltip>
                <Tooltip content="" direction="top"><span></span></Tooltip>
                <Tooltip content="" direction="top-right"><span></span></Tooltip>
            </div>
            <div class="center">
                <Tooltip direction="left">
                    <p slot="content"></p>
                    <span></span>
                </Tooltip>
                <span></span>
                <Tooltip direction="right" content="">
                    <span></span>
                </Tooltip>
            </div>
            <div class="bottom">
                <Tooltip direction="bottom-left" content=""><span></span></Tooltip>
                <Tooltip direction="bottom" content=""><span></span></Tooltip>
                <Tooltip direction="bottom-right" content=""><span></span></Tooltip>
            </div>
        </div>
        <h3>2</h3>
        <p></p>
        <p>&nbsp;</p>
        <div class="tooltip-demo">
            <div class="top">
                <Tooltip content="X" :maxWidth="500"><span></span></Tooltip>
                <Tooltip content="50%" direction="top"><span></span>
                </Tooltip>
                <Tooltip content="" direction="top-right" :maxWidth="200"><span></span>
                </Tooltip>
            </div>
            <div class="center">
                <Tooltip direction="left" :maxWidth="500">
                    <p slot="content"></p>
                    <span></span>
                </Tooltip>
                <span></span>
                <Tooltip direction="right" content="" :maxWidth="200">
                    <span></span>
                </Tooltip>
            </div>
            <div class="bottom">
                <Tooltip direction="bottom-left" content="" :maxWidth="200"><span></span>
                </Tooltip>
                <Tooltip direction="bottom" content=""><span></span>
                </Tooltip>
                <Tooltip direction="bottom-right" content=""><span></span>
                </Tooltip>
            </div>
        </div>
        <h3>3</h3>
        <p>icon</p>
        <p>&nbsp;</p>
        <div class="tooltip-demo">
            <Tooltip content="icon"><i class="icon">?</i></Tooltip>
            <Tooltip content="" className="align"><i class="icon">?</i></Tooltip>
        </div>
        <h3>4</h3>
        <p>&nbsp;</p>
        <p>&nbsp;</p>
        <div class="tooltip-demo">
            <div class="top">
                <Tooltip content="" :maxWidth="500" direction="top" :always="true"><span>always</span></Tooltip>
            </div>
        </div>
        <h2>API</h2>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>content</td>
                <td>String</td>
                <td>slot</td>
            </tr>
            <tr>
                <td>className</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>direction</td>
                <td>String</td>
                <td>top-lefttoptop-rightleftrightbottom-leftbottombottom-right8</td>
            </tr>
            <tr>
                <td>maxWidth</td>
                <td>Number500</td>
                <td></td>
            </tr>
            <tr>
                <td>delay</td>
                <td>Number200</td>
                <td></td>
            </tr>
            <tr>
                <td>always</td>
                <td>Booleanfalse</td>
                <td></td>
            </tr>
        </table>
    </div>
</template>
<script>
    import Tooltip from '../components/tooltip/index'
    export default {
        name: 'tooltip',
        path: '/tooltip',
        data () {
            return {}
        },
        props: {},
        components: {Tooltip},
        methods: {
            click(){
            }
        },
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>
<style>
    .tooltip-demo{ width: 350px; margin-left: 100px; text-align: center; }
    .tooltip-demo span{ display: inline-block; vertical-align: middle; width: 55px; height: 30px; line-height: 30px; text-align: center; background: #f7f7f7; border: 1px solid #dddee1; border-radius: 3px; cursor: pointer; transition: all .5s; }
    .tooltip-demo span:hover{ background: #fff; border-color: #36a1d3; }
    .center{ padding: 40px 0; overflow: hidden; }
    /*.center span{ float: left; }
    .center span:nth-child(2){ float: right; }*/
    .tooltip-box{ margin: 0 5px; }
    .tooltip-demo .icon{ width: 20px; height: 20px; line-height: 20px; text-align: center; font-style: normal; background: #ddd; display: inline-block; border-radius: 50%; cursor: pointer }
    /**/
    .tooltip.align{background: #f60}
    .tooltip.align:before{ left: 7px;border-top-color: #f60 }
</style>


