<template>
    <div class="demo">
        <comHeader name="datePicker"/>
        <h1>DatePicker Demo</h1>
        <h2>Use</h2>
        <pre>import DatePicker from '../components/datePicker/index'</pre>
        <pre>&lt;DatePicker v-model="value">&lt;/DatePicker></pre>
        <h2></h2>
        <p>bodyoverflow:hidden</p>
        <h3>1</h3>
        <p> value:{{value}}</p>
        <DatePicker placeholder="select" v-model="value"></DatePicker>
        <h3>2ymd</h3>
        <DatePicker placeholder="select" v-model="value2" format="ymd"></DatePicker>
        <h3>3</h3>
        <DatePicker v-model="value3" format="y"></DatePicker>
        <h3>4 ym</h3>
        <DatePicker v-model="value4" format="ym"></DatePicker>
        <h3>5 yM</h3>
        <DatePicker v-model="value5" format="yM"></DatePicker>
        <h3>6 ymdHms</h3>
        <DatePicker v-model="value6" format="ymdHms"></DatePicker>
        <h3>7 yMDHms</h3>
        <DatePicker v-model="value7" format="yMDHms" placeholder=""></DatePicker>
        <h3>8disabled</h3>
        <DatePicker v-model="value8" format="yMDHms" placeholder="" :disabled="true"></DatePicker>
        <h3>9disabledDate</h3>
        <p></p>
        <pre>
        disabledDate9(time){
            var day = time.getDay();
            return day === 0 || day === 6;
        }</pre>
        <DatePicker v-model="value9" placeholder="" :disabledDate="disabledDate9"></DatePicker>
        <h3>10disabledDate</h3>
        <pre>
        disabledDate10(time){
           let min = new Date(2017, 10, 20);
           let max = new Date(2017, 11, 20);
           return time < min || time > max
        }</pre>
        <DatePicker placeholder="2017-11-202017-12-20" :disabledDate="disabledDate10" v-model="value10"></DatePicker>
        <h3>11inline</h3>
        <pre>
            import Picker from '../components/datePicker/datePicker'
            &lt;Picker v-model="value11">&lt;/Picker></pre>
        <p>{{value11}}</p>
        <div style="height: 230px;position: relative">
            <Picker v-model="value11" type="inline"></Picker>
        </div>
        <h3>12range</h3>
        <p>yMd</p>
        <DatePicker placeholder="2017-11-202017-12-20" :disabledDate="disabledDate10" type="range"
                    v-model="value12"></DatePicker>
        <h3>13innerHTML</h3>
        <p></p>
        <pre>
            innerHTML(time){
                let start = new Date(2017, 11, 30);
                let end = new Date(2018, 0, 1);
                if (time >= start && time <= end) {
                    return ''
                }
            }</pre>
        <DatePicker placeholder="2018" v-model="value13" :innerHTML="innerHTML"></DatePicker>
        <h2>API</h2>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>value</td>
                <td></td>
                <td>v-model</td>
            </tr>
            <tr>
                <td>placeholder</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>showClear</td>
                <td>Booleantrue</td>
                <td>input</td>
            </tr>
            <tr>
                <td>disabledDate</td>
                <td>Function</td>
                <td>return true</td>
            </tr>
            <tr>
                <td>format</td>
                <td>String</td>
                <td>y()ym()yM()ymd()yMD()ymdHms()yMDHms()</td>
            </tr>
            <tr>
                <td>disabled</td>
                <td>Booleanfalse</td>
                <td></td>
            </tr>
            <tr>
                <td>type</td>
                <td>Stringnormal</td>
                <td>normalinlinerange</td>
            </tr>
            <tr>
                <td>split</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>innerHTML</td>
                <td>Function</td>
                <td></td>
            </tr>
        </table>
    </div>
</template>
<script>
    import DatePicker from '../components/datePicker/index'
    import Picker from '../components/datePicker/datePicker'
    export default {
        name: 'datePicker',
        path: '/datePicker',
        data () {
            return {
                value: '',
                value2: '2017-2-20',
                value3: '2017',
                value4: '2017-8',
                value5: '2017-08',
                value6: '2017-8-8 08:08:08',
                value7: '',
                value8: '2017-12-20',
                value9: '',
                value10: '',
                value11: '',
                value12: '2017-11-232017-12-12',
                value13: ''
            }
        },
        props: {},
        components: {DatePicker, Picker},
        methods: {
            disabledDate9(time){
                var day = time.getDay();
                return day === 0 || day === 6;
                /*let min = new Date(2017, 10, 10,0,0,0);
                 let max = new Date(2017, 11, 20);
                 return time < min || time > max*/

            },
            disabledDate10(time){
                let min = new Date(2017, 10, 20);
                let max = new Date(2017, 11, 20);
                return time < min || time > max

            },
            innerHTML(time){
                let start = new Date(2017, 11, 30);
                let end = new Date(2018, 0, 1);
                if (time >= start && time <= end) {
                    return ''
                }
            }
        },
        computed: {
            formatTime(){

            }
        },
        mounted(){
        },
        filters: {},
        watch: {
            value(){
                // console.log('page value change')
            }
        }
    }
</script>


