<template>
    <div class="demo">
        <comHeader name="cascader"/>
        <h1>Cascader Demo</h1>
        <h2>Use</h2>
        <pre>import Cascader from '../components/cascader/index'</pre>
        <pre>&lt;Cascader v-model="value">&lt;/Cascader></pre>
        <h2></h2>
        <p>js</p>
        <h3>1</h3>
        <p>{{value1}}</p>
        <Cascader v-model="value1"></Cascader>
        <h3>2</h3>
        <p>{{value2}}</p>
        <Cascader v-model="value2"></Cascader>
        <h3>3placeholder</h3>
        <p>{{value3}}</p>
        <Cascader v-model="value3" placeholder=""></Cascader>
        <h3>4disabled</h3>
        <Cascader v-model="value4" placeholder="" :disabled="true"></Cascader>
        <h3>5</h3>
        <pre>
            &lt;Cascader v-model="value3" placeholder="" tipsText="" :selectText="selectText" :split="split">&lt;/Cascader>
           selectText: ['1', '2', '3'],
           split: [',', ',', '']</pre>
        <Cascader v-model="value5" placeholder="" tipsText="" :selectText="selectText"
                  :split="split"></Cascader>
        <h3>6change</h3>
        <Cascader v-model="value6" placeholder="" :change="_change"></Cascader>
        <h3>7</h3>
        <p>{{value7}}</p>
        <Cascader v-model="value7" placeholder="" :data="data7"></Cascader>
        <pre>&lt;Cascader v-model="value7" placeholder="" :data="data7">&lt;/Cascader></pre>
        <pre>
            data7: [
                    {
                        name: '', children: [
                        {
                            name: '', children: [
                            '', '', '', ''
                        ]
                        },
                        {name: ''},
                        {name: ''}
                    ]
                    },
                    {
                        name: '', children: [
                        {name: ''},
                        {name: ''},
                        {name: ''},
                        {name: ''}
                    ]
                    },
                    {
                        name: '', children: [
                        {name: ''},
                        {name: ''},
                        {name: ''},
                        {name: ''}
                    ]
                    },
                    {
                        name: '', children: [
                        {name: ''}
                    ]
                    }
                ]
        </pre>
        <h2>API</h2>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>value</td>
                <td>Array</td>
                <td>v-model</td>
            </tr>
            <tr>
                <td>placeholder</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>disabled</td>
                <td>Boolean|false</td>
                <td></td>
            </tr>
            <tr>
                <td>tipsText</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>selectText</td>
                <td>Array</td>
                <td>['', '', '']</td>
            </tr>
            <tr>
                <td>split</td>
                <td>Arraynull</td>
                <td></td>
            </tr>
            <tr>
                <td>data</td>
                <td>Array</td>
                <td></td>
            </tr>
            <tr>
                <td>change</td>
                <td>Function</td>
                <td>v-model</td>
            </tr>
        </table>
        <h3>data</h3>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>name</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>children</td>
                <td>Array</td>
                <td>name</td>
            </tr>
        </table>
    </div>
</template>
<script>
    import Cascader from '../components/cascader/index'
    export default {
        name: 'cascader',
        path: '/cascader',
        data () {
            return {
                value1: [],
                value2: ['', '', ''],
                value3: [],
                value4: [],
                value5: [],
                value6: [],
                value7: [],
                selectText: ['1', '2', '3'],
                split: [',', ',', ''],
                data7: [
                    {
                        name: '', children: [
                        {
                            name: '', children: [
                            '', '', '', ''
                        ]
                        },
                        {name: ''},
                        {name: ''}
                    ]
                    },
                    {
                        name: '', children: [
                        {name: ''},
                        {name: ''},
                        {name: ''},
                        {name: ''}
                    ]
                    },
                    {
                        name: '', children: [
                        {name: ''},
                        {name: ''},
                        {name: ''},
                        {name: ''}
                    ]
                    },
                    {
                        name: '', children: [
                        {name: ''}
                    ]
                    }
                ]
            }
        },
        props: {},
        components: {Cascader},
        methods: {
            _change(v){
                console.log(v)
            }
        },
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


