<template>
    <div class="demo">
        <comHeader name="textarea"/>
        <h1>TextArea Demo</h1>
        <h2>Use</h2>
        <pre>import gTextArea from '../components/textarea/index'</pre>
        <pre>&lt;gTextArea placeholder="placeholder" v-model="value">&lt;/gTextArea></pre>
        <h2></h2>
        <p>1</p>
        <h3>1</h3>
        <p>{{value}}</p>
        <gTextArea placeholder="placeholder" v-model="value"></gTextArea>
        <h3>2</h3>
        <gTextArea placeholder="placeholder" :disabled="true"></gTextArea>
        <h3>3</h3>
        <gTextArea placeholder="placeholder" class="abc" width="80%" height="120px"
                   v-model="value3" :autoHeight="false"></gTextArea>
        <h3>4</h3>
        <gTextArea placeholder="placeholder" v-model="value4" @focus="focus" :focus="focus2"></gTextArea>
        <h2>API</h2>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td></td>
                <td></td>
                <td>classplaceholderdisabledreadonly...</td>
            </tr>
            <tr>
                <td>value</td>
                <td>String</td>
                <td>v-model</td>
            </tr>
            <tr>
                <td>width</td>
                <td>String100%</td>
                <td>px/%</td>
            </tr>
            <tr>
                <td>height</td>
                <td>String80px</td>
                <td></td>
            </tr>
            <tr>
                <td>autoHeight</td>
                <td>Booleantrue</td>
                <td></td>
            </tr>
            <tr>
                <td>change</td>
                <td>Function</td>
                <td>v-onv-bind</td>
            </tr>
            <tr>
                <td>focus</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>blur</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>keyup</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>keydown</td>
                <td>Function</td>
                <td></td>
            </tr>
        </table>
    </div>
</template>
<script>
    import gTextArea from '../components/textarea/index'
    export default {
        name: 'textarea',
        path: '/textarea',
        data(){
            return {
                value: '',
                value3: 'width="80%";height="120px";autoHeight="false"\n',
                value4: '<gTextArea placeholder="placeholder" v-model="value4" @focus="focus" :focus="focus2"></gTextArea>'
            }
        },
        props: [],
        mounted(){
        },
        components: {gTextArea},
        methods: {
            focus(){
                console.log('focus');
            },
            focus2(){
                console.log('focus2');
            },
            _event(){
                console.log('_event');
            }
        },
        computed: {}
    }
</script>


