<template>
    <div class="demo">
        <comHeader name="carousel"/>
        <h1>Carousel Demo</h1>
        <h2></h2>
        <pre>
            import {Carousel, CarouselItem} from '../components/carousel/index'</pre>
        <pre>
            &lt;Carousel>
            &lt;CarouselItem>&lt;img src="../assets/images/1.jpg">&lt;/CarouselItem>
            &lt;CarouselItem>&lt;img src="../assets/images/1.jpg">&lt;/CarouselItem>
            &lt;/Carousel></pre>
        <h2></h2>
        <Carousel className="class-name" :slideAfter="slideAfter">
            <CarouselItem><img src="../assets/images/1.jpg"></CarouselItem>
            <CarouselItem><img src="../assets/images/2.jpg"></CarouselItem>
            <CarouselItem><img src="../assets/images/3.jpg"></CarouselItem>
            <CarouselItem><img src="../assets/images/4.jpg"></CarouselItem>
        </Carousel>
        <p>&nbsp;</p>
        <h2></h2>
        <Carousel className="class2" animation="slide" :autoPlay="false" :loop="true">
            <CarouselItem><img src="../assets/images/3.jpg"></CarouselItem>
            <CarouselItem><img src="../assets/images/1.jpg"></CarouselItem>
            <CarouselItem><img src="../assets/images/2.jpg"></CarouselItem>
            <CarouselItem></CarouselItem>
        </Carousel>
        <p>&nbsp;</p>
        <h3>Carousel</h3>
        <table class="table-1">
            <tr>
                <th width="80"></th>
                <th width="120"></th>
                <th></th>
            </tr>
            <tr>
                <td>class</td>
                <td>String</td>
                <td></td>
            </tr>
            <tr>
                <td>animation</td>
                <td>String | fade</td>
                <td>fadeslide</td>
            </tr>
            <tr>
                <td>loop</td>
                <td>Boolean | true</td>
                <td></td>
            </tr>
            <tr>
                <td>speed</td>
                <td>Number | 300</td>
                <td></td>
            </tr>
            <tr>
                <td>showTime</td>
                <td>Number | 3000</td>
                <td></td>
            </tr>
            <tr>
                <td>autoPlay</td>
                <td>Boolean | true</td>
                <td></td>
            </tr>
            <tr>
                <td>pauseOnHover</td>
                <td>Boolean | true</td>
                <td></td>
            </tr>
            <tr>
                <td>controlNav</td>
                <td>Boolean | true</td>
                <td></td>
            </tr>
            <tr>
                <td>directionNav</td>
                <td>Boolean | true</td>
                <td></td>
            </tr>
            <tr>
                <td>slideTo</td>
                <td>Number | 0</td>
                <td></td>
            </tr>
            <tr>
                <td>after</td>
                <td>Function</td>
                <td></td>
            </tr>
            <tr>
                <td>slideAfter</td>
                <td>Function</td>
                <td></td>
            </tr>
        </table>
        <p>&nbsp;</p>
        <p>slide</p>
    </div>
</template>
<script>
    import {Carousel, CarouselItem} from '../components/carousel/index'
    export default {
        name: "carousel",
        path: '/carousel',
        data () {
            return {}
        },
        components: {Carousel, CarouselItem},
        mounted(){
        },
        methods: {
            slideAfter(index){
                // console.log('' + index);
            }
        },
        filters: {}
    }
</script>
<style>
    .carousel-slide img{ width: 100%;height: 100% }
</style>


