<template>
    <div class="demo">
        <comHeader name="inputNumber"/>
        <h1>InputNumber Demo</h1>
        <h2></h2>
        <pre>import InputNumber from '../components/inputNumber/index'</pre>
        <pre>&lt;InputNumber v-model="value" :max="10">&lt;/InputNumber></pre>
        <h3>1</h3>
        <InputNumber :max="10"></InputNumber>
        <h3>2</h3>
        <p>{{value}}</p>
        <p>change{{callBack}}</p>
        <InputNumber v-model="value" :change="change" :step="2" :max="10"></InputNumber>
        <h3>3</h3>
        <p></p>
        <InputNumber :disabled="disabled"></InputNumber>
        <h2>API</h2>
        <table class="table-1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <td>class</td>
                <td>null</td>
                <td>divclass</td>
            </tr>
            <tr>
                <td>value</td>
                <td>Number1</td>
                <td>v-model</td>
            </tr>
            <tr>
                <td>min</td>
                <td>Number0</td>
                <td></td>
            </tr>
            <tr>
                <td>max</td>
                <td>Number0</td>
                <td></td>
            </tr>
            <tr>
                <td>step</td>
                <td>Number1</td>
                <td></td>
            </tr>
            <tr>
                <td>disabled</td>
                <td>Booleanfalse</td>
                <td></td>
            </tr>
            <tr>
                <td>change</td>
                <td>Function</td>
                <td></td>
            </tr>
        </table>
    </div>
</template>
<script>
    import InputNumber from '../components/inputNumber/index'
    export default {
        name: 'inputNumber',
        path: '/inputNumber',
        data(){
            return {
                value: 1,
                callBack: '',
                disabled:true
            }
        },
        props: [],
        mounted(){
        },
        components: {InputNumber},
        methods: {
            change(v){
                this.callBack = v
            }
        },
        computed: {}
    }
</script>


