<!--example
<Page :total="100"></Page>-->
<template>
    <div class="page">
        <slot/>
        <a href="javascript:;" title="" class="prev" :class="{'disabled':active==1}" @click="_goTo(-1)"></a>
        <a href="javascript:;" title="1" @click="_goTo(1)" :class="{'active':active==1}" v-if="active>=showNum">1</a>
        <a href="javascript:;" :title="titleTipP" class="jump-prev" @click="_jumpTo(-1)" v-if="active>=showNum"></a>
        <a href="javascript:;" :title="item" v-for="item in lists" v-text="item" :class="{'active':item==active}"
           @click="_goTo(item)"></a>
        <a href="javascript:;" :title="titleTipN" class="jump-next" @click="_jumpTo(1)" v-if="jumpNext"></a>
        <a href="javascript:;" :title="pageCount" @click="_goTo(pageCount)" :class="{'active':active==pageCount}"
           v-if="pageCount>=1">{{pageCount}}</a>
        <a href="javascript:;" title="" class="next" :class="{'disabled':pageCount<=active}" @click="_goTo(0)"></a>
        <span class="show-elevator" v-if="showElevator">
            <input type="text" v-model="goToPage" class="page-input" maxlength="5">
        </span>
    </div>
</template>
<script>
    export default {
        name: '',
        data () {
            return {
                active: this.value,//
                pageCount: Math.ceil(this.total / this.pageSize),//
                goToPage: 1//
            }
        },
        created(){

        },
        props: {
            value: {//
                type: Number,
                default: 1
            },
            total: {//
                type: Number,
                default: 0
            },
            pageSize: {//
                type: Number,
                default: 10
            },
            showElevator: {//
                type: Boolean,
                default: true
            },
            showNum: {//
                type: Number,
                default: 5
            },
            change: Function//
        },
        components: {},
        mounted(){
        },
        watch: {
            goToPage(){
                if (parseInt(this.goToPage)) {
                    let page = parseInt(this.goToPage);
                    if (page > this.pageCount) {
                        page = this.pageCount;
                    } else if (page < 1) {
                        page = 1
                    }
                    this._goTo(page);
                }
            }
        },
        methods: {
            _goTo(page){
                //-100
                if (page === -1 && this.active > 1) {
                    this.active--;
                } else if (page === 0 && this.active < this.pageCount) {
                    this.active++;
                } else if (page > 0 && page <= this.pageCount) {
                    this.active = page;
                }
                this.$emit('input', this.active);
                this.change ? this.change(this.active) : "";
            },
            _jumpTo(type){
                //N
                let page;
                if (type > 0) {
                    //+
                    if (this.active + this.showNum > this.pageCount) {
                        page = this.pageCount
                    } else {
                        page = this.active + this.showNum
                    }
                } else {
                    //
                    if (this.active - this.showNum < 1) {
                        page = 1
                    } else {
                        page = this.active - this.showNum
                    }
                }
                this._goTo(page);
            }
        },
        computed: {
            titleTipP(){
                return `${this.showNum}`
            },
            titleTipN(){
                //
                return `${this.showNum}`
            },
            jumpNext(){
                //this.showNumthis.showNum+1
                //this.active+this.showNum/2<this.pageCount,
                //this.showNumshowNum
                if (this.active + Math.round(this.showNum / 2) < this.pageCount) {
                    return true
                } else {
                    return false
                }
            },
            lists(){
                let start = 1;
                let end = this.pageCount - 1;//1
                //end=active+
                if (this.active + parseInt(this.showNum / 2) < this.pageCount) {
                    end = this.active + parseInt(this.showNum / 2)
                }
                if (this.active >= this.showNum) {
                    start = this.active - parseInt(this.showNum / 2)
                }
                //
                let array = [];
                while (start <= end) {
                    array.push(start);
                    start++;
                }
                return array;
            }
        },
        filters: {}
    }
</script>


